import { VariantLike } from 'node-opcua';

export interface OpcuaInterface {
  init(): Promise<void>;

  disconnect(): Promise<void>;

  readValue<T>(nodeId: string): Promise<T | undefined>;

  callMethod<T>(
    nodeId: string,
    methodName: string,
    args?: Array<VariantLike>,
  ): Promise<Array<T>>;

  callFromDsl<T>(dsl: string): Promise<Array<T>>;

  subscribeToNode<T>(
    nodeId: string,
    callback: (nodeId: string, value: T) => void,
  ): Promise<void>;

  unsubscribeFromNode(nodeId: string): Promise<void>;
}
